<template>
    <div>
        <b-modal id="deactiveSupplier" hide-footer hide-header centered>
            <template v-slot:modal-title>
                <h5 class="modal-title">Supplier Management</h5>
            </template>
            <div class="d-block">
                <div class="modal-body mt-4 mb-3" align="center">
                    <div class="mt-4 mb-4">
                        <img v-if="supplierStatus" src="@/assets/images/icons/tick.png">
                        <img v-else src="@/assets/images/icons/warning-message-icon.png">
                    </div>
                    <h3>Are you sure</h3>
                    <p v-if="supplierStatus" class="mb-5">you want to activate this supplier?</p>
                    <p v-else class="mb-5">you want to deactivate this supplier?</p>
                    <button type="button" class="btn btn-outline-primary btn-lg mr-3" @click="$bvModal.hide('deactiveSupplier')">Cancel</button>
                    <button type="button" class="btn btn-primary btn-lg" v-on:click="activeDeactiveStatus();">Ok</button>
                </div>
            </div>
        </b-modal>
        <div class="d-block header-section">
            <div class="py-3 px-3 align-self-center d-sm-flex w-100 rounded">
                <div class="w-sm-100 mr-auto">
                    <h1>
                        <b>Supplier Management</b>
                    </h1>
                </div>
                <div class="float-right"> <button type="button" class="btn btn-link" v-on:click="loadnewSupplier()"><img src="@/assets/images/icons/add.png" alt=""> NEW SUPPLIER</button></div>
            </div>
        </div>
        <div class="container-fluid productManagement">
            <!-- START: Card Data-->
            <div class="row">
                <div class="col-12 col-md-12">
                    <div class="card ">
                        <div class="card-header p-0">
                            <b-collapse id="supplier" v-model="showNewSupplier">
                                <div class="col-12 col-sm-11 ml-5 mr-4 mt-5">
                                    <div class="form-row ml-3 mb-4">
                                        <div class="form-group row col-md-6">
                                            <label for="username" class="col-sm-3 col-form-label pr-0 pl-0">Supplier Name</label>
                                            <div class="col-sm-8">
                                                <div class="form-group" :class="{ 'form-group--error': $v.newSupplier.name.$error }">
                                                    <input type="text" class="form-control" placeholder="Supplier Name" v-model="newSupplier.name">
                                                    <div v-if="addSupplierErrors">
                                                        <span class="text-danger" v-if="!$v.newSupplier.name.required">Supplier Name is Required</span>
                                                        <span class="text-danger" v-else-if="!$v.newSupplier.name.checkSupplierNameDuplicate">Supplier Name already exists</span>
                                                    </div>
                                                </div>
                                            </div>
                                        </div>
                                        <div class="form-group row col-md-6">
                                            <label for="username" class="col-sm-3 col-form-label pr-0 pl-0">License Number</label>
                                            <div class="col-sm-8">
                                                <div class="form-group" :class="{ 'form-group--error': $v.newSupplier.license.$error }">
                                                    <input type="text" class="form-control" placeholder="License Number" v-model="newSupplier.license">
                                                    <div v-if="addSupplierErrors">
                                                        <span class="text-danger" v-if="!$v.newSupplier.license.required">License is Required</span>
                                                        <span class="text-danger" v-else-if="!$v.newSupplier.license.checkSupplierLicenseDuplicate">License already exists</span>
                                                    </div>
                                                </div>
                                            </div>
                                        </div>
                                    </div>
                                    <div class="form-row ml-3 mb-4">
                                        <div class="form-group row col-md-6">
                                            <label for="username" class="col-sm-3 col-form-label pr-0 pl-0">Contact Name</label>
                                            <div class="col-sm-8">
                                                <div class="form-group" :class="{ 'form-group--error': $v.newSupplier.contactName.$error }">
                                                    <input type="text" class="form-control" placeholder="Contact Name" v-model="newSupplier.contactName">
                                                    <div v-if="addSupplierErrors">
                                                        <span class="text-danger" v-if="!$v.newSupplier.contactName.required">Contact Name is Required</span>
                                                    </div>
                                                </div>
                                            </div>
                                        </div>
                                        <div class="form-group row col-md-6">
                                            <label for="username" class="col-sm-3 col-form-label pr-0 pl-0">Email Id</label>
                                            <div class="col-sm-8">
                                                <div class="form-group" :class="{ 'form-group--error': $v.newSupplier.email.$error }">
                                                    <input type="text" class="form-control" placeholder="Email Id" v-model="newSupplier.email" :disabled="newSupplier.id!=0">
                                                    <div v-if="addSupplierErrors">
                                                        <span class="text-danger" v-if="!$v.newSupplier.email.required">Email Id is Required</span>
                                                        <span class="text-danger" v-else-if="!$v.newSupplier.email.email">Emai id is not valid</span>
                                                        <span class="text-danger" v-else-if="!$v.newSupplier.email.checkEmailDuplicate">Emai id already exists</span>
                                                    </div>
                                                </div>
                                            </div>
                                        </div>
                                    </div>

                                    <div class="form-row ml-3 mb-4">
                                        <div class="form-group row col-md-6">
                                            <label for="username" class="col-sm-3 col-form-label pr-0 pl-0">Contact Number</label>
                                            <div class="col-sm-8">
                                                <div class="input-group mb-3">
                                                    <div class="form-group col-sm-4 p-0" :class="{ 'form-group--error': $v.newSupplier.countryCode.$error }">
                                                        <input type="text" class="form-control " placeholder="+233" v-model="newSupplier.countryCode">
                                                        <div v-if="addSupplierErrors">
                                                            <span class="text-danger" v-if="!$v.newSupplier.countryCode.required">Country code is required</span>
                                                        </div>
                                                    </div>
                                                    <div class="form-group col-sm-8 pr-0" :class="{ 'form-group--error': $v.newSupplier.phone.$error }">
                                                        <input type="text" class="form-control" v-model="newSupplier.phone" placeholder="89654123656">
                                                        <p v-if="addSupplierErrors"> <span class="text-danger" v-if="!$v.newSupplier.phone.required">Contact Number is required</span></p>
                                                    </div>
                                                </div>
                                            </div>
                                        </div>
                                        <div class="form-group row col-md-6">
                                            <label for="username" class="col-sm-3 col-form-label pr-0 pl-0">Country</label>
                                            <div class="col-sm-8">
                                                <div class="form-group" :class="{ 'form-group--error': $v.newSupplier.country.$error }">
                                                    <select class="form-control" v-model="newSupplier.country">
                                                        <option value=""> Select Location</option>
                                                        <option :key="index" v-for="(country,index) in $store.getters.countries" :value="country.name">{{country.name}}</option>
                                                    </select>
                                                    <div v-if="addSupplierErrors">
                                                        <span class="text-danger" v-if="!$v.newSupplier.country.required">Country is Required</span>
                                                        <span class="text-danger" v-else-if="!$v.newSupplier.country.checkNullOrEmpty">Country is not valid</span>
                                                    </div>
                                                </div>
                                            </div>
                                        </div>

                                    </div>
                                    <div class="form-row ml-3">
                                        <div class="form-group row col-md-6">
                                            <label for="username" class="col-sm-3 col-form-label pr-0 pl-0">Suppliers Address</label>
                                            <div class="col-sm-8">
                                                <div class="form-group" :class="{ 'form-group--error': $v.newSupplier.address.$error }">
                                                    <textarea class="form-control" placeholder="Suppliers Address" v-model="newSupplier.address"></textarea>
                                                    <div v-if="addSupplierErrors">
                                                        <span class="text-danger" v-if="!$v.newSupplier.address.required">Address is Required</span>
                                                    </div>
                                                </div>
                                            </div>
                                        </div>
                                        <div class="form-group row col-md-6">
                                            <label for="username" class="col-sm-3 col-form-label pr-0 pl-0">Postal Address</label>
                                            <div class="col-sm-8">
                                                <div class="form-group" :class="{ 'form-group--error': $v.newSupplier.postalCode.$error }">
                                                    <input type="text" class="form-control" placeholder="Postal Address" v-model="newSupplier.postalCode" />
                                                    <div v-if="addSupplierErrors">
                                                        <span class="text-danger" v-if="!$v.newSupplier.postalCode.required">Postal Code is Required</span>
                                                    </div>
                                                </div>
                                            </div>
                                        </div>
                                    </div>
                                    <button class="btn btn-primary float-right mb-4 pl-5 pr-5 p-2 " name="save" value="SAVE" type="button" v-on:click="addUpdateSupplier()">SUBMIT</button>
                                    <button class="btn btn-outline-primary float-right mb-4 pl-5 pr-5 mr-3 p-2" name="save" value="CANCEL" type="button" v-on:click="clearData()">CANCEL</button>
                                </div>

                                <div class="clearfix"></div>
                            </b-collapse>
                            <div class="list-filter">
                                <div class="btn-group">
                                    <b-form-radio id="All" name="radios" autocomplete="off" class="custom-control-input" checked :value="allSuppliersCount" />
                                    <label for="All" type="button" class="btn btn-outline-dark rounded-btn mr-2" @click="onStatusFilterClicked('All')">ALL({{ allSuppliersCount }})</label>
                                </div>
                                <div class="btn-group">
                                    <b-form-radio name="radios" autocomplete="off" class="custom-control-input" id="Active" :value="activeSuppliersCount" />
                                    <label type="button" class="btn btn-outline-warning rounded-btn mr-2 mob-mar" for="Active" @click="onStatusFilterClicked('Active')">Active({{activeSuppliersCount}})</label>
                                </div>
                                <div class="btn-group">
                                    <b-form-radio name="radios" autocomplete="off" class="custom-control-input" id="InActive" :value="inActiveSuppliersCount" />
                                    <label type="button" class="btn btn-outline-primary rounded-btn mr-2 mob-mar" for="InActive" @click="onStatusFilterClicked('InActive')">InActive({{ inActiveSuppliersCount }})</label>
                                </div>
                            </div>
                            <div class="col-12 col-sm-12 p-0">
                                <div class="search-tbl">
                                        <div class="form-row col-md-12">
                                            <div class="col-md-4 mt-3">
                                                <b-input-group>
                                                    <b-form-input v-model="searchPattern" class="form-control" type="search" id="filterInput" placeholder="search- Name, location, Email"></b-form-input>
                                                    <b-input-group-append>
                                                        <b-button :disabled="!searchPattern" @click="searchPattern = ''">Clear</b-button>
                                                    </b-input-group-append>
                                                </b-input-group>
                                            </div>
                                        </div>
                                    </div>
                                <b-table id="supplierList"
                                         :items="getSuppliers"
                                         :filter="searchPattern"
                                         filter-debounce="1000"
                                         :fields="dataFields"
                                         :current-page="currentPage"
                                         :per-page="perPage"
                                         class="table order">
                                    <template v-slot:cell(status)="data">
                                        <span :class="data.item.status==='Active'?'text-success':'text-danger'">{{data.item.status}}</span>
                                    </template>
                                    <template v-slot:cell(action)="data">
                                        <b-button variant="link" class="text-primary pr-2" @click="editSupplier(data.item.supplierId)"><img src="@/assets/images/icons/edit-gray.png" /> Edit</b-button>
                                        <b-button variant="link" v-if="data.item.status==='Active'" @click="confirmBox(data.item.supplierId,false)"><img src="@/assets/images/icons/activate.png" />Deactivate</b-button>
                                        <b-button variant="link" v-else @click="confirmBox(data.item.supplierId,true)"><img src="@/assets/images/icons/deactivate-new.png" /> Activate</b-button>
                                    </template>
                                </b-table>
                                <b-pagination v-model="currentPage" aria-controls="supplierList" :total-rows="totalRows" :per-page="perPage" class="float-right mr-4" first-text="First" prev-text="Prev" next-text="Next" last-text="Last">
                                </b-pagination>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
            <!-- END: Card DATA-->
        </div>
    </div>
</template>
<script>
    import { required, email } from 'vuelidate/lib/validators';
    import { checkNullOrEmpty, checkEmailDuplicate, checkSupplierNameDuplicate, checkSupplierLicenseDuplicate } from '@/Validations';
    import toast from '@/toast';
    import axios from 'axios';
    export default {
        data() {
            return {
                searchPattern: '', totalRows: 1, currentPage: 1, perPage: 10, pageOptions: [5, 10, 15],
                dataFields: [{ key: 'name', label: 'Name', sortable: true }, { key: 'location', label: 'Location', sortable: true },
                { key: 'email', label: 'Email', sortable: true }, { key: 'status', label: 'Status', sortable: true },
                { key: 'action', label: 'Action', sortable: true }],
                newSupplier: this.assignSupplierFields(), allSuppliersCount: 0, activeSuppliersCount: 0,
                inActiveSuppliersCount: 0, addSupplierErrors: false, suppliersList: [], showNewSupplier: false,
                supplierStatus: true, suppId: '', filterStatus: 'All'
            };
        },
        async mounted() {
            await this.$store.dispatch("getCountriesAsync");
        },
        validations: {
            newSupplier: {
                name: {
                    required,
                    checkSupplierNameDuplicate(value) {
                        if (this.newSupplier.id !== 0) return true;
                        return checkSupplierNameDuplicate(value);
                    }
                },
                license: {
                    required,
                    checkSupplierLicenseDuplicate(value) {
                        if (this.newSupplier.id !== 0) return true;
                        return checkSupplierLicenseDuplicate(value);
                    }
                },
                contactName: { required },
                email: {
                    required, email,
                    checkEmailDuplicate(value) {
                        if (this.newSupplier.id !== 0) return true;
                        return checkEmailDuplicate(value);
                    }
                },
                phone: { required },
                address: { required },
                postalCode: { required },
                countryCode: { required },
                country: {
                    required, checkNullOrEmpty
                }
            }
        },
        components: {},
        methods: {
            assignSupplierFields() {
                var obj = { id: 0, name: '', license: '', contactId: 0, contactName: '', countryCode: '+91', email: '', phone: '', country: '', addressId: 0, address: '', postalCode: '' };
                return obj;
            },
            loadnewSupplier() {
                this.newSupplier = this.assignSupplierFields(); this.addSupplierErrors = false;
                this.showNewSupplier = true;
            },
            getSuppliers(ctx) {
                const promise = axios({
                    url: '/Supplier/GetSuppliers',
                    method: 'post',
                    baseURL: process.env.VUE_APP_VmApisBaseUrl,
                    data: {
                        PageSize: ctx.perPage, PageNumber: ctx.currentPage,
                        SearchByParameter: this.searchPattern, SearchByStatus: this.filterStatus
                    }
                })
                return promise.then(response => {
                    const items = response.data.miniSupplierDtos;
                    this.totalRows = response.data.totalCount;
                    this.allSuppliersCount = response.data.totalCount;
                    this.activeSuppliersCount = response.data.activeCount;
                    this.inActiveSuppliersCount = response.data.inActiveCount;
                    return items || [];
                })
            },
            onStatusFilterClicked(status) {
                this.filterStatus = status;
                this.gridRefresh();
            },
            gridRefresh() {
                this.$root.$emit('bv::refresh::table', 'supplierList');
            },
            async addUpdateSupplier() {
                this.$v.$touch();
                this.addSupplierErrors = this.$v.newSupplier.$anyError;
                if (!this.addSupplierErrors) {
                    var countryId = this.$store.getters.countries.filter(element => element.name === this.newSupplier.country).map(element => element.id);
                    var objSupplier = {
                        id: this.newSupplier.id, name: this.newSupplier.name, identifier1: this.newSupplier.license,
                        identifier2: this.newSupplier.license, isActive: true, contact: {
                            email: this.newSupplier.email, id: this.newSupplier.contactId,
                            name: this.newSupplier.contactName, countryCode: this.newSupplier.countryCode, phone: this.newSupplier.phone
                        },
                        address: {
                            id: this.newSupplier.addressId, description: this.newSupplier.address, postalAddress: this.newSupplier.postalCode,
                            country: { id: parseInt(countryId), name: this.newSupplier.country, regions: null }
                        }
                    };
                    this.newSupplier = this.assignSupplierFields();
                    this.showNewSupplier = false;
                    const promise = axios({
                        url: "/Supplier/",
                        method: 'post',
                        baseURL: process.env.VUE_APP_VmApisBaseUrl,
                        data: objSupplier
                    })
                    promise.then(response => {
                        if (response.status == 200) {
                            toast.success("Supplier Saved!..");
                            this.gridRefresh();
                        }
                    });
                }
            },
            editSupplier(supplierId) {
                const promise = axios({
                    url: "/Supplier/" + supplierId,
                    method: 'get',
                    baseURL: process.env.VUE_APP_VmApisBaseUrl,
                })
                promise.then(response => {
                    if (response.status == 200) {
                        var currentSupplier = response.data;
                        this.newSupplier = {
                            id: currentSupplier.id, name: currentSupplier.name,
                            license: currentSupplier.identifier1, contactId: currentSupplier.contact.id, contactName: currentSupplier.contact.name,
                            email: currentSupplier.contact.email, phone: currentSupplier.contact.phone, countryCode: currentSupplier.contact.countryCode,
                            country: currentSupplier.address.country.name, addressId: currentSupplier.address.id, address: currentSupplier.address.description,
                            postalCode: currentSupplier.address.postalAddress
                        };
                        this.showNewSupplier = true;
                    }
                });
            },
            activeDeactiveStatus() {
                this.$root.$emit('bv::hide::modal', 'deactiveSupplier');
                const promise = axios.post(process.env.VUE_APP_VmApisBaseUrl + "/Supplier/ActivateOrDeactivateSupplier", null, { params: { id: this.suppId, status: this.supplierStatus } });
                promise.then(response => { if (response.status == 200) this.gridRefresh(); });
            },
            confirmBox(id, status) {
                this.supplierStatus = status;
                this.suppId = id;
                this.$root.$emit('bv::show::modal', 'deactiveSupplier');
            },
            clearData() {
                this.newSupplier = this.assignSupplierFields();
                this.showNewSupplier = false;
            }
        }
    }
</script>